@import url(https://fonts.googleapis.com/css?family=Coda|Galindo&display=swap);
html {
  scroll-behavior: smooth;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --main-colour: #707070;
  --main-bg-colour: #ffffff;
  --off-main-bg-colour: #dddddd;
  --dark-bg-colour: #000000;
  --off-dark-bg-colour: #222222;
}
.app {
  display: grid;
  place-items: center center;
  min-height: 100vh;
  background: var(--main-bg-colour);
  font-family: 'Galindo', display;
  color: var(--main-colour);
}
input {
  font-family: 'Galindo', display;
}
textarea {
  font-family: 'Coda', display;
}
.app.dark {
  background: var(--dark-bg-colour);
}
.clickable {
  cursor: pointer;
}
.icon-button {
  background: none;
  border: none;
  cursor: pointer;
}
.pageHeader {
    width: 100%;
    display: grid;
    grid-template-columns: 2fr 1fr;
    overflow: hidden;
    place-self: start center;
    height: 120px;
    z-index: 300;
    background: var(--main-bg-colour);
}
.dark .pageHeader {
    background: var(--dark-bg-colour);
}
.pageHeader__logo {
    place-self: center center;
    width: 45vw;
    margin-left: 5vw;
    max-width: 320px;
}
.pageHeader__logo--condensed {
    display: none;
}
.pageHeader__menu {
    place-self: center end;
    display: grid;
    grid-template-columns: 50vw;
    grid-template-rows: repeat(3, 1fr);
    -webkit-transform: translateX(calc(50vw - 150px));
            transform: translateX(calc(50vw - 150px));
}
.pageHeader__link {
    margin-top: 6px;
    border: 1px solid var(--main-colour);
    border-left: none;
    position: relative;
    height: 22px;
    -webkit-transition: -webkit-transform 0.5s;
    transition: -webkit-transform 0.5s;
    transition: transform 0.5s;
    transition: transform 0.5s, -webkit-transform 0.5s;
    text-decoration: none;
    color: var(--main-colour);
}
.pageHeader__link::before {
    content: "";
    display: block;
    position: absolute;
    top: -1px;
    left: -11px;
    border-left: 5px solid var(--main-colour);
    border-right: none;
    border-radius: 50%;
    width: 24px;
    height: 24px;
}
.pageHeader__link--active, .pageHeader__link:hover {
    -webkit-transform: translateX(-10vw);
            transform: translateX(-10vw);
}
@media only screen and (max-width: 640px) { 
    .pageHeader__logo {
        display: none;
    }
    .pageHeader__logo--condensed {
        display: block;
        margin-left: 5vw;
        place-self: center center;
        width: 200px;
        max-width: 50vw;
    }
    .pageHeader__menu {
        
        -webkit-transform: translateX(18vw);
        
                transform: translateX(18vw);
    }
}
.toolbar {
    width: 90%;
    max-width: 1200px;
    margin-top: 24px;
    display: grid;
    grid-template-columns: 2fr 2fr 1fr 1fr;
    grid-template-rows: auto;
    grid-column-gap: 5vw;
    place-items: center center;
    grid-template-areas: 
      ". font . ."
      "search font display reset"
      ". font . .";
    padding: 0 5vw 0 5vw;
    color: var(--main-colour);
    place-self: start center;
}
.toolbar__search {
    grid-area: search;
    display: flex;
    flex-flow: row nowrap;
    max-width: 25vw;
}
.toolbar__search__input {
    width: 100%;
    min-width: 100px;
    margin-left: 8px;
    border: none;
    font-size: 24px;
    max-width: 200px;
    background: var(--main-bg-colour);
    color: var(--main-colour);
}
.dark .toolbar__search__input { 
  background: var(--dark-bg-colour);
}
.toolbar__search__input::-webkit-input-placeholder {
    color: var(--main-colour);
    opacity: 0.8;
}
.toolbar__search__input:-ms-input-placeholder {
    color: var(--main-colour);
    opacity: 0.8;
}
.toolbar__search__input::-ms-input-placeholder {
    color: var(--main-colour);
    opacity: 0.8;
}
.toolbar__search__input::placeholder {
    color: var(--main-colour);
    opacity: 0.8;
}
.toolbar__font-control {
    grid-area: font;
}
.toolbar__display-control {
    grid-area: display;
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-between;
    width: 12vw;
    max-width: 80px;
}
.toolbar__reset {
    grid-area: reset;
}

.toolbar__custom-text {
    width: 100%;
    min-width: 140px;
    margin-right: 24px;
    border: none;
    font-size: 24px;
    background: var(--main-bg-colour);
    color: var(--main-colour);
}
.dark .toolbar__custom-text { 
  background: var(--dark-bg-colour);
}
.toolbar__custom-text::-webkit-input-placeholder {
    color: var(--main-colour);
    opacity: 0.8;
}
.toolbar__custom-text:-ms-input-placeholder {
    color: var(--main-colour);
    opacity: 0.8;
}
.toolbar__custom-text::-ms-input-placeholder {
    color: var(--main-colour);
    opacity: 0.8;
}
.toolbar__custom-text::placeholder {
    color: var(--main-colour);
    opacity: 0.8;
}
.toolbar__font-size {
    margin-left: 24px;
    display: flex;
    flex-direction: column;
}
.toolbar__font-size__label {
    align-self: center;
}

.toolbar__set-font-size[type='range'] {
  overflow: hidden;
  max-width: 200px;
  -webkit-appearance: none;
  background-color: none;
  outline: none;
  background: linear-gradient(180deg, 
        rgba(0,0,0,0) calc(50% - 1px), 
        rgba(192,192,192,1) calc(50%), 
        rgba(0,0,0,0) calc(50% + 1px));
}
.toolbar__set-font-size[type='range']::-webkit-slider-thumb {
  width: 15px;
  -webkit-appearance: none;
  height: 15px;
  cursor: ew-resize;
  background: var(--main-bg-colour);
  border: 1px solid var(--main-colour);
  border-radius: 50%;
}
.dark .toolbar__set-font-size[type='range']::-webkit-slider-thumb {
  background: var(--dark-bg-colour);
}

@media only screen and (max-width: 1000px) {
    .toolbar {
        display: grid;
        grid-template-columns: repeat(3, auto);
        grid-template-rows: auto;
        grid-gap: 24px;
        place-items: center center;
        grid-template-areas: 
        "search display display"
        "font font reset";
    }
}
@media only screen and (max-width: 640px) {
  .toolbar {
    padding: 0;
    grid-gap: 0;
  }
  .toolbar__layout-toggle {
    grid-area: layout;
  }
  .toolbar__theme-toggle {
    grid-area: theme;
  }
  .toolbar__display-control {
    display: grid;
    grid-template-columns: repeat(2, auto);
    grid-template-rows: auto;
    grid-gap: 0;
    place-items: center center;
    grid-template-areas: 
    "layout ."
    ". theme";
  }
  .toolbar__custom-text {
    font-size: calc(2vw + 13px);
  }
}
.footer {
    width: 100%;
    height: 120px;
    margin-top: 200px;
    padding-bottom: 24px;
    background-color: var(--main-colour);
    display: grid;
    place-items: center center;
    place-self: end center;
    z-index: 300;
}
.github-link {
    width: 192px;
    height: 64px;
    display: grid;
    place-items: center center;
    grid-template-columns: 64px 64px 64px;
    grid-gap: 12px;
    gap: 12px;
    text-decoration: none;
    font-size: 14px;
    color: var(--main-bg-colour);
}
.dark .github-link {
    color: var(--dark-bg-colour);
}
.github-logo--light, .dark .github-logo--dark {
    display: block;
}
.dark .github-logo--light, .github-logo--dark {
    display: none;
}
.github-link__left-text {
    text-align: right;
}
.github-link__right-text {
    text-align: left;
}
.card {
    display: grid;
    place-items: center center;
    color: var(--main-colour);
    text-align: center;
}
.grid .card {
    max-width: 40vw;
    width: 200px;
}
.bar .card {
    width: 80vw;
    max-width: 1200px;
    margin-bottom: 24px;
}
.card__heading {
    cursor: pointer;
    font-size: 22px;
}
.grid .card__heading {
    position: relative;
    display: grid;
    place-items: start center;
    width: 100%;
}
.grid .card__heading::before {
    position: absolute;
    content: '';
    background: url(/solo-koala-54/static/media/cardgridheaderbg.e32af5ea.svg) no-repeat;
    background-size: 100% 100%;
    width: 100%;
    height: 100%;
}
.bar .card__heading {
    position: relative;
    display: grid;
    place-items: center center;
    width: 100%;
}
.bar .card__heading::before {
    position: absolute;
    content: '';
    background: url(/solo-koala-54/static/media/cardheaderbarbg.be69a4e6.svg) no-repeat;
    background-size: 100% 100%;
    width: 80vw;
    max-width: 1200px;
    height: 18px;
}
.card__heading__title {
    background: var(--main-bg-colour);
    z-index: 100;
    max-width: 80%;
}
.dark .card__heading__title {
    background: var(--dark-bg-colour);
}
.grid .card__heading__title {
    margin-top: 24px;
}
.bar .card__heading__title {
    margin-top: -16px;
} 
.card__text {
    margin-top: 0;
    text-align: left;
    word-break: break-word;
}
.bar .card__text {
    width: 85%;
}
.card__collection-button {
    position: absolute;
    left: 5px;
    top: calc(50% - 7px);
}
.card__collection-button--add {
    visibility: hidden;
}
.card__heading:hover .card__collection-button--add { 
    visibility: visible;
}
@media only screen and (max-width: 640px) { 
    .grid .card__heading {
        font-size: 3.5vw;
    }
    .grid .card__heading__title {
        margin-top: 3.5vw;
    }
}
.catalog {
    width: calc(100% - 24px);
    margin-left: 24px;
    max-width: 1200px;
    margin-top: 48px;
    display: grid;
    place-items: start center;
    place-self: start center;
}
.catalog.grid {
    display: grid;
    grid-template-columns: repeat(4, 1fr);  
    max-width: 1200px;
}
@media only screen and (max-width: 1200px) {
    .catalog.grid {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
    }
}
@media only screen and (max-width: 920px) {
    .catalog.grid {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
    }
}
@media only screen and (max-width: 640px) {
    .catalog.grid {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
    }
}
.collection-open-button {
    position: fixed;
    left: 0;
    bottom: 25%;
    z-index: 200;
    opacity: 0.8;
    padding: 14px 8px 14px 18px;
    text-align: left;
    color: var(--main-colour);
    -webkit-transform: translateX(calc(-100% + 32px));
            transform: translateX(calc(-100% + 32px));
    -webkit-transition: -webkit-transform 1s;
    transition: -webkit-transform 1s;
    transition: transform 1s;
    transition: transform 1s, -webkit-transform 1s;
    background: var(--off-main-bg-colour);
    display: grid;
    grid-template-columns: auto 24px;
    grid-template-areas: 'list title';
    place-items: center end;
    border-radius: 8px;
}
.collection-open-button__title {
    -webkit-transform: rotate(-90deg) translateY(58px);
            transform: rotate(-90deg) translateY(58px);
    grid-area: title;
    font-size: 18px;
    letter-spacing: 3px;
}
.dark .collection-open-button { 
    background: var(--off-dark-bg-colour);
}
.collection-open-button__list {
    grid-area: list;
    -webkit-padding-start: 10px;
            padding-inline-start: 10px;
}
.collection-open-button.show, .collection-open-button:hover {
    -webkit-transform: translateX(-10%);
            transform: translateX(-10%);
}
.collection {
    width: 80%;
    max-width: 1200px;
    border: solid 1px var(--main-colour);
    border-radius: 12px;
    background: var(--off-main-bg-colour);
    padding: 5vw;
    display: grid;
    grid-template-columns: repeat(2, auto);
    grid-template-rows: repeat(3, auto);
    grid-template-areas: 
    "list embed"
    "list import"
    "list css";
    position: relative;
    color: var(--main-colour);
}
.collection textarea {
    width: 100%;
    resize: none;
}
.collection__close {
    position: absolute;
    right: 24px;
    top: 24px;
    cursor: pointer;
}
.collection__title {
    font-size: 32px;
}
.collection__list {
    width: 200px;
    grid-area: list;
    place-self: start center;
}
.collection__code {
    color: var(--main-colour);
}
.dark .collection__code {
    background: var(--dark-bg-colour);
}
.collection__embed {
    grid-area: embed;
    display: grid;
    grid-template-columns: repeat(2, auto);
}
.collection__embed .collection__code {
    min-height: 50px;
}
.collection__import {
    grid-area: import;
}

.collection__import, .collection__embed, .collection__css__font-family {
    display: grid;
    grid-template-columns: 40px auto;
    grid-template-rows: repeat(2, auto);
    grid-template-areas: 
    "subtitle subtitle"
    "copy code";
    place-items: center start;
}
.collection__css {
    grid-area: css;
}
.collection__css__font-family {
    margin-bottom: 12px;
}
.collection__css__font-family .collection__copy-button {
    height: 24px;
}
.collection__list .card {
    width: 100%;
}
.collection .card__text {
    display: none;
}
.collection__subtitle {
    grid-area: subtitle;
}
.collection__copy-button {
    grid-area: copy;
}
.collection__code {
    grid-area: code;
}
.collection .card__heading {
    position: relative;
    width: 100%;
    font-size: 24px;
    text-align: left;
    padding-left: 24px;
}
.collection .card__heading::before {
    position: absolute;
    content: '';
    background: none;
}
.collection .card__heading__title {
    margin-top: 24px;
    background: var(--off-main-bg-colour);
}
@media only screen and (max-width: 640px) { 
    .collection {
        grid-template-columns: auto;
        grid-template-rows: repeat(4, auto);
        grid-template-areas: 
        "list"
        "embed"
        "import"
        "css";
        grid-gap: 0 5vw;
        gap: 0 5vw;
    }
}
.back-to-top {
    position: fixed;
    bottom: 156px;
    right: 24px;
    z-index: 300;
    background: var(--main-bg-colour);
}
.dark .back-to-top {
    background: var(--dark-bg-colour);
}
.featured {
    width: 80%;
    max-width: 1200px;
    display: grid;
    place-items: start center;
    place-self: start center;
}



.featured__roboto {

}
.featured__noto-fonts {

}
.featured__open-sans {

}
.about {
    width: 80%;
    max-width: 1200px;
    display: grid;
    place-items: start center;
    place-self: start center;
}
